<template>
  <div>
    <div class="root">
      <div class="ro">
        <div class="root2" :style="'background-image:url(' + img + ')'"></div>
      </div>
      <p class="logo">
        <img src="@/assets/1609923779(1).png" alt="" />
        网易云音乐
      </p>
      <div class="tu">
        <div class="gq">
          <img src="@/assets/needle-ip6.png" alt="" class="gqimg" />
          <div class="zx">
            <img :src="img" alt="" />
          </div>
        </div>
        <p>{{ name }}</p>
        <audio
          :src="url.url"
          controls="controls"
          autoplay
          style="margin-left:33%;margin-top:99px"
        ></audio>
      </div>
    </div>
    <div class="bottom">
      <div>打开</div>
      <div class="div2">下载</div>
    </div>
