</template>
<script>
export default {
  data() {
    return {
      url: {},
      img: null,
      name: null,
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      let id = this.$route.query.id;
      let url = this.$route.query.url;
      let name = this.$route.query.name; // eslint-disable-line no-unused-vars
      this.img = url;
      this.name = name;
      this.$http.post("api/song/url?id=" + id + "").then((res) => {
        this.url = res.data.data[0];
        console.log(this.url);
      });
    },
  },
};
</script>
